@import url(https://fonts.googleapis.com/css2?family=Archivo+Black&family=Open+Sans&display=swap);
/* @import url('https://fonts.googleapis.com/css2?family=Oswald&family=Quattrocento&display=swap'); */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #010101;
  font-family: 'Open Sans', sans-serif;
  color: #acdbdf;
}

/* 
---------Color-Palette--------- 
black - #010101
blue - #69779b
aqua-blue - #acdbdf;
white - #f0ece2
-------------------------------
*/

/* ---------BG-Colors--------- */
.bg-black {background-color: #010101;}
.bg-blue {background-color: #69779b;}
.bg-light-blue {background-color: #acdbdf;}
.bg-white {background-color: #f0ece2;}



/* ---------Grid-Container--------- */
.container {
  width: 95%;
  max-width: 1024px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: minmax(300px, auto);
  grid-gap: 1rem;
  padding-top: 4rem;
}

.align-center {
  align-items: center;
}

/* ---------Headers--------- */
.h1,.h2,.h3,.h4,.h5,.h6,.h1-big,.h2-big,.h3-big,.h4-big {
  font-family: 'Archivo Black', sans-serif;
  color: #acdbdf;
  /* text-transform: uppercase; */
}
.h1 {font-size: 4rem;}
.h2 {font-size: 3.5rem;}
.h3 {font-size: 3rem;}
.h4 {font-size: 2.5rem;}
.h5 {font-size: 2rem;}
.h6 {font-size: 1.5rem;}
.h1-big {font-size: 9rem;}
.h2-big {font-size: 7.5rem;}
.h3-big {font-size: 6rem;}
.h4-big {font-size: 4.5rem;}

/* ---------Text-Styles--------- */
.text-center {text-align: center;}
.text-justify {text-align: justify;}
.text-left {text-align: left;}
.text-right {text-align: right;}
.text-light-blue {color: #acdbdf}
.text-blue {color: #69779b}
.text-white {color: #f0ece2}
.text-black {color: #010101}
.text-secondary {font-family: 'Quattrocento', sans-serif}

/* ---------Buttons--------- */
.button {
  display: grid;
  color: #acdbdf;
  background-color: #010101;
  border: 1px solid #acdbdf;
  transition: color 0.1s linear, background-color 0.15s linear, border 0.1s linear;
  cursor: pointer;
}
.button p:only-child {
  font-family: 'Archivo Black', sans-serif;
  margin-left: 1rem;
  margin-right: 1rem;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  font-size: 1.25rem;
}
.button a {
  text-decoration: none;
  color: #acdbdf;
}
.button a:hover {
  text-decoration: none;
  color: #010101;
}
.button:hover {
  color: #010101;
  background-color: #acdbdf;
  border: 1px solid #010101;
}
.button:active {
  transform: translate(0.8px,0.8px);
  color: #010101b6;
  background-color: #acdbdfaf;
}

/* ---------Icon-Images--------- */
.icon {
  height: 2.5rem;
  width: 2.5rem;
  margin: 0.5em;
  
}
.icon-button {cursor: pointer;}

/* ---------Loading-Screen--------- */
.loading-screen {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}



@media screen and (max-width: 768px) {
  .button {
    color: #010101;
    background-color: #acdbdf;
  }
  .button a {
    text-decoration: none;
    color: #010101;
  }
}
/* ---------Navbar--------- */
.nav {
  display: grid;
  width: 100%;
  height: 64px;
  background-color: #010101;
  align-items: center;
  justify-items: center;
  position: fixed;
  z-index: 10;
}
.nav-list-item{
  display: inline-block;
}
.nav-item {
  color: #f0ece2;
  background-color: #010101;
  font-family: 'Oswald', sans-serif;
  font-size: 1.5rem;
  padding: 0.5rem 1rem;
  text-decoration: none;
  transition: color 0.15s linear;
}
.nav-item:active {
  background-color: #7272728a;
}
.nav-item:hover {
  background-color: #7272728a;
}
.nav-item-active {
  color: #010101;
  background-color: #acdbdf;
}
.nav-item-active:hover {
  color: #010101;
  background-color: #acdbdf;
}

/* ---------Welcome-Component--------- */
.welcome {
  height: 90vh;
  grid-column: 1/7;
  display: grid;
  align-items: center;
}
.welcome-wrap {
  grid-column: 1 / 7;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1rem;
  justify-items: center;
}
.welcome-text-1 {
  grid-column: 1 / 5;
}
.welcome-text-2 {
  grid-column: 1 / 5;
}
.welcome-text-3 {
  grid-column: 1 / 5;
  margin-top: 0.5rem;
}
.welcome-button {
  grid-column: span 4;
  display: flex;
  justify-items: center;
  margin-top: 0.5rem;
}
.welcome-button-link {
  text-decoration: none;
  color: #acdbdf;
}
.welcome-button-link:hover {
  color: #010101;
}

/* ---------Devicons--------- */
.welcome-linkdin {
  margin-top: 0.5rem;
  grid-column: 2/3;
  height: 3rem;
}
.welcome-github {
  grid-column: 3/4;
  height: 3rem;
  margin-top: 0.5rem;
}
.devicon path {
  fill: #f0ece2;
}






@media screen and (max-width: 768px) {
  .welcome-text-1 {
    grid-column: 1 / 5;
    font-size: 3rem;
  }
  .welcome-text-2 {
    grid-column: 1 / 5;
    font-size: 2rem;
  }
  .welcome-text-3 {
    grid-column: 1 / 5;
    margin-top: 0.5rem;
    font-size: 1rem;
  }
  .welcome-button {
    background-color: #acdbdf;
  }
  .welcome-button-link, .welcome-button-link p {
    color: #010101;
  }
}
/* -----------Projects-Styles----------- */
.projects {
   grid-column: 1/7;
   display: grid;
   /* grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); */
   grid-template-columns: repeat(2, 1fr);
   grid-gap: 3rem;
   grid-template-rows: minmax(320px, auto);
   margin-bottom: 1rem;
   justify-content: space-evenly;
   margin-top: 3rem;
}













@media screen and (max-width: 768px) {
  /* .projects {
     justify-content: center;
     align-items: center;
  } */
}
/* ----------Card-Styles---------- */
.card {
   background-color: #222222;
   grid-column: span 1;
   width: 80%;
   justify-self: center;
}
.card-img img {
   margin-bottom: 0.5rem;
}
.card-title {
   margin-bottom: 0.5rem;
}
.card-link-project {
   font-size: 1rem;
   display: flex;
   justify-content: space-around;
   margin-bottom: 0.5rem;
   margin-left: 0.5rem;
   margin-right: 0.5rem;
}
.card-link-project a {
   text-decoration: none;
   color: #f0ece2;
   background-color: #010101;
   transition: 0.15s linear;
}
.card-link-project a:hover {
   color: #010101;
   background-color: #acdbdf;
}
.card-link-project a p {
   text-align: center;
   padding: 0.5rem 1rem;
}
.card-link-site {
   font-size: 1rem;
   display: flex;
   justify-content: center;
   margin-bottom: 0.5rem;
   margin-left: 0.5rem;
   margin-right: 0.5rem;
}
.card-link-site a {
   text-decoration: none;
   color: #f0ece2;
   background-color: #010101;
   transition: 0.15s linear;
}
.card-link-site a:hover {
   color: #010101;
   background-color: #acdbdf;
}
.card-link-site a p {
   text-align: center;
   padding: 0.5rem 1rem;
}
.card-badge {
   margin-bottom: 0.5rem;
}










@media screen and (max-width: 768px) {
   .card {
      grid-column: span 7;
      width: 88%;
   }
   .card-link-project a {
      color: #010101;
      background-color: #acdbdf;
   }
   .card-link-site a {
      color: #010101;
      background-color: #acdbdf;
   }
}
/* -----------Badge-Styles----------- */
.badge {
   background-color: #010101;
   max-width: -webkit-fit-content;
   max-width: -moz-fit-content;
   max-width: fit-content;
   padding: 0.3rem 0.6rem;
   margin: 0 0.2rem;
   display: inline-block;
   margin-bottom: 0.3rem;
}

.badge p {
   color: #f0ece2;
   font-size: 0.7rem;
}
/* ----------About-Styles---------- */
.about {
   grid-column: span 7;
   justify-self: center;
   padding-top: 1.5rem;
}
.about-me {
   display: grid;
   grid-template-columns: 1fr 1fr;
   grid-gap: 1rem;
   padding-left: 1rem;
   padding-right: 1rem;
   margin-bottom: 3rem;
}
.about-me-img{
   display: flex;
   justify-content: center;
}
.about-me-img img {
   -webkit-filter: grayscale(1);
           filter: grayscale(1);
}
.about-me-text {
   display: flex;
   flex-direction: column;
   margin: auto 0;
}
.about-me-text > p {
   text-align: justify;
   margin-bottom: 1rem;
}


/* ---------Contact-Section---------- */
.contact > p:nth-child(2) {
   margin-bottom: 1.5rem;
}
.contact > p:nth-child(1) {
   margin-bottom: 1rem;
}
.contact-subhead {
   font-size: 1.12rem;
}
.contact-link {
   display: flex;
   justify-content: space-evenly;
   margin-top: 1.5rem;
}











@media screen and (max-width: 768px) {
   .about {
      margin-bottom: 4.5rem;
   }
   .about-me {
      flex-direction: column;
      display: flex;
      margin-bottom: 1.5rem;
   }
   .about-me-text {
      margin-bottom: 0;
   }
   .about-me-img {
      display: flex;
      justify-content: center;
      margin-top: 1.5rem;
      margin-bottom: 1.5rem;
   }
   .about-me-img img {
      height: 72%;
      width: 72%;
   }
   .about-me-text > p {
      text-align: center;
   }
   .contact-heading {
      font-size: 1.8rem;
   }
}
